<template>
<div class="user">
	<div class="user-bg">
		<div v-if="!isLogin">
			<h3 class="welcome-title">Hello!</h3>
			<p class="welcome-content">登录享受更多精彩服务</p>
			<van-button color="#c2a67e" round size="mini" :to="{ name: 'Login' }">立即登录</van-button>
		</div>
		<div class="user" v-if="isLogin">
			<div class="user-img">
				<img class="img-layout" :src="userInfo.userImg" alt="">
			</div>
			<div class="user-name">
				<p>{{userInfo.nickName}}</p>
				<p class="des">{{userInfo.desc ? userInfo.desc : '主人很懒，啥也没留下~'}}</p>
			</div>
		</div>	
	</div>

	<div class="user-list">
		<van-cell is-link :title="u.title" :icon="u.icon" v-for="(u,i) in userListData" :key="i" @click="showDetail(u.name)" />
	</div>
</div>
</template>

<script>
import '../../assets/scss/user.scss'
import {
	mapGetters	
} from 'vuex'

export default {
	data() {
		return {
			userListData: [
				{
					title: '个人资料',
					icon: 'contact',
					name: 'UserInfo'
				},
				{
					title: '我的订单',
					icon: 'orders-o',
					name: 'Order'
				},
				{
					title: '我的收藏',
					icon: 'like-o',
					name: 'Like'
				},
				{
					title: '我的地址',
					icon: 'location-o',
					name: 'MyAddress'
				},
				{
					title: '数据分析',
					icon: 'chart-trending-o',
					name: 'ChartView'
				},
				{
					title: '设置',
					icon: 'setting-o',
					name: 'UserSetting'
				}
			],
			isLogin: false,
			userInfo: {}
		}
	},
	created() {
		this.getUserInfo();
	},
	methods: {
		// 查看列表详情
		showDetail(name) {
			if(!this.getToken) {
				this.$router.push({
					name: 'Login'
				})
				return ;
			}

			this.$router.push({
				name
			})
		},
		// 获取用户信息
		getUserInfo() {
			if(!this.getToken) {
				return ;
			}

			let params = {
				appkey: this.appkey,
				tokenString: this.getToken
			}

			this.$toast({
				type: 'loading',
				message: '加载中',
				forbidClick: true,
				loadingType: 'spinner',
				duration: 0
			});

			this.axios({
				method: 'GET',
				url: '/findMy',
				params
			})
			.then(res => {
				this.$toast.clear();
				console.log(res);
				if(res.data.code === 'A001') {
					this.isLogin = true;
					this.userInfo = res.data.result[0];
				}
				
			})
			.catch(err => {
				this.$toast.clear();
				console.log(err);
				
			})
		}
	},
	computed: {
		...mapGetters(['getToken'])
	}
}
</script>

<style>

</style>